@import url(https://fonts.googleapis.com/css2?family=Reenie+Beanie);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
@import url(https://fonts.googleapis.com/css2?family=Cutive+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cutive+Mono&family=Itim&family=Nanum+Pen+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Special+Elite&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: left;
 font-family: 'Reenie Beanie', cursive;


}
li {
  text-align: left;
 font-size: 28px;
 font-family: 'Reenie Beanie';
  font-weight: bold;
 
}

.work-experience-item {
	 text-align: left;
 font-size: 28px;
 font-family: 'Reenie Beanie';
  font-weight: bold;
}

.work-experience-header {
 text-align: left;
 font-size: 36px;
 font-family: 'Reenie Beanie';
  font-weight: bold;
}

.big-header {
 text-align: left;
 font-size: 42px;
 font-family: 'Reenie Beanie';
  font-weight: bold;
}



h1 {
margin: auto
}

.canvas {

height:20%;
}

.hidden {
display:none;
}

.column {
  float: left;
  width: 25%;
  padding: 10px;
 
}

.sticky {
  float: left;
  width: 200px;
  padding: 12px;

}

.column-fillremainder {
  float: left;
  width: 100%;
  padding: 10px;

}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {

  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
canvas.dotted {border-style: dotted}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

